import React, { useState } from 'react'; import './Gallery.css';
function Gallery() {

const initialItems = [
{
title: 'Ï†ïÎ≥¥Í∏∞Ïà†Í¥Ä',
author: 'ÏÜ°ÏäπÏ§Ä',
date: '2024-06-23',
content: 'Í∏∞Î≥∏ ÎÇ¥Ïö© 1',
likes: 0,
dislikes: 0,
},
{
title: 'Î≤ïÌïôÍ¥Ä',
author: 'ÏÜ°ÏäπÏ§Ä',
date: '2024-06-23',
content: 'Í∏∞Î≥∏ ÎÇ¥Ïö© 2',
likes: 0,
dislikes: 0,
},
{
title: 'Î∞±Ï£ºÎÖÑÍ∏∞ÎÖêÍ¥Ä',
author: 'ÏÜ°ÏäπÏ§Ä',
date: '2024-06-23',
content: 'Í∏∞Î≥∏ ÎÇ¥Ïö© 3',
likes: 0,
dislikes: 0,
}
];

const [listItems, setListItems] = useState(initialItems);
const [inputTitle, setInputTitle] = useState('');
const [inputAuthor, setInputAuthor] = useState('');
const [inputDate, setInputDate] = useState('');
const [inputContent, setInputContent] = useState('');
const [selectedTitle, setSelectedTitle] = useState(null);

const handleInputChange = (e, setState) => {
setState(e.target.value);
};

const handleAddList = () => {
if (inputTitle.trim() === '' || inputAuthor.trim() === ''|| inputContent.trim()===''||inputDate.trim()==='') {
alert('ÎÇ¥Ïö©ÏùÑ ÏûÖÎ†•Ìï¥Ï£ºÏÑ∏Ïöî.');
return;
}


// ÏûÖÎ†•Ìïú Ï†ïÎ≥¥Î•º Î¶¨Ïä§Ìä∏Ïóê Ï∂îÍ∞Ä
setListItems([
  ...listItems,
  {
    title: inputTitle,
    author: inputAuthor,
    date: inputDate,
    content: inputContent,
    likes: 0,
    dislikes: 0,
  },
]);

// ÏûÖÎ†• ÌïÑÎìú Ï¥àÍ∏∞Ìôî
setInputTitle('');
setInputAuthor('');
setInputDate('');
setInputContent('');
};

const handleDeleteList = (index) => {
const newItems = [...listItems];
newItems.splice(index, 1);
setListItems(newItems);
};

return (
<div className="App">
<div className="black-nav">
<h4>ReactBlog</h4>
</div>
<div>
<input
type="text"
value={inputTitle}
onChange={(e) => handleInputChange(e, setInputTitle)}
placeholder="Ï†úÎ™©ÏùÑ ÏûÖÎ†•ÌïòÏÑ∏Ïöî"
/>
<input
type="text"
value={inputAuthor}
onChange={(e) => handleInputChange(e, setInputAuthor)}
placeholder="ÏûëÏÑ±ÏûêÎ•º ÏûÖÎ†•ÌïòÏÑ∏Ïöî"
/>
<input
type="date"
value={inputDate}
onChange={(e) => handleInputChange(e, setInputDate)}
/>
<textarea
value={inputContent}
onChange={(e) => handleInputChange(e, setInputContent)}
placeholder="ÎÇ¥Ïö©ÏùÑ ÏûÖÎ†•ÌïòÏÑ∏Ïöî"
/>
<button onClick={handleAddList}>Ï†ÄÏû•</button>
</div>
{listItems.map((item, index) => (
<List
key={index}
title={item.title}
author={item.author}
date={item.date}
content={item.content}
likes={item.likes}
dislikes={item.dislikes}
onLike={() => {
const newItems = [...listItems];
newItems[index].likes++;
setListItems(newItems);
}}
onDislike={() => {
const newItems = [...listItems];
newItems[index].dislikes++;
setListItems(newItems);
}}
onClick={() => setSelectedTitle(index)}
onDelete={() => handleDeleteList(index)}
/>
))}
{selectedTitle !== null && <Modal title={listItems[selectedTitle].title} content={listItems[selectedTitle].content} />}
</div>
);
}

function List({ title, likes, dislikes, onLike, onDislike, onClick, onDelete, date }) {
return (
<div className="list">
<h4 onClick={onClick}>{title}</h4>
<span onClick={onLike}>üëç {likes}</span>
<span onClick={onDislike}>üëé {dislikes}</span>
<button onClick={onDelete}>ÏÇ≠Ï†ú</button>
<p>{date}</p>
</div>
);
}

function Modal({ title, content }) {
return (
<div className="modal">
<h4>{title}</h4>
<p>ÏÉÅÏÑ∏ÎÇ¥Ïö©: {content}</p>
</div>
);
}

export default Gallery;
